"use client";

import { useState } from "react";
import { THours, TContact, TContactForm } from "@/types";
import { api } from "@/data/data-api";
import { getStrapiURL } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import {
  Field,
  FieldGroup,
  FieldLabel,
} from "@/components/ui/field";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { MailIcon, MapPinIcon, PhoneIcon } from "lucide-react";
import { Textarea } from "../ui/textarea";

// ============================================
// SCHEDULE COMPONENT
// ============================================

export interface IScheduleProps {
  id: number;
  __component: string;
  heading: string;
  subHeading: string;
  hours: THours[];
}

export function Schedule({ data }: { data: IScheduleProps }) {
    if (!data?.hours) return null;

    const { heading, subHeading, hours } = data;
    return (
        <div className="w-full max-w-xl mx-auto px-8 pb-6">
            <div className="rounded-xl bg-brand-pink border my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col">
                <h2 className="text-xl font-bold text-white mb-4">{heading}</h2>
                <p className="text-center text-white mb-4">{subHeading}</p>
                <div>
                    {hours.map((item: THours) => (
                        <div key={item.id} className="flex justify-between py-2">
                            <div className="font-bold text-white">{item.days}</div>
                            <div className="font-bold text-white">{item.times}</div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}

// ============================================
// CONTACT INFO COMPONENT
// ============================================

export interface IContactInfoProps {
  id: number;
  __component: string;
  heading: string;
  subHeading: string;
  contact: TContact[];
}

export function ContactInfo({ data }: { data: IContactInfoProps }) {
    if (!data?.contact) return null;

    const { heading, subHeading, contact } = data;
    return (
        <div className="w-full max-w-xl mx-auto px-8 py-6 mb-6">
            <div className="rounded-xl border bg-white my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col">
                <h2 className="text-xl font-semibold">{heading}</h2>
                <p className="text-lg text-brand-dark-grey mb-2">{subHeading}</p>
                <div className="py-4">
                    {contact.map((item: TContact) => (
                        <div key={item.id} className="mb-4 space-y-3">
                            <div className="flex items-start gap-3">
                                <PhoneIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold">Phone Number</h3>
                                    <p className="text-gray-600">{item.phone}</p>
                                </div>
                            </div>
                            <div className="flex items-start gap-3">
                                <MailIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold text-brand-dark-grey">Email</h3>
                                    <p className="text-black">{item.email}</p>
                                </div>
                            </div>
                            <div className="flex items-start gap-3">
                                <MapPinIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold">Location</h3>
                                    <p className="text-gray-600">{item.location}</p>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

// ============================================
// CONTACT FORM COMPONENT
// ============================================

export interface IContactFormLabelsProps {
  id: number;
  __component: string;
  heading: string;
  subHeading: string;
  name: string;
  email: string;
  phone: string;
  location: string;
  instagram: string;
  experience: string;
  interest: string;
  clients: string;
  info: string;
}

type FormData = {
  name: string;
  email: string;
  phone: string;
  location: string;
  instagram: string;
  experience: string;
  interest: string;
  clients: string;
  info: string;
};

export function ContactFormLabels({ data }: { data: IContactFormLabelsProps }) {
  const [formData, setFormData] = useState<FormData>({
    name: "",
    email: "",
    phone: "",
    location: "",
    instagram: "",
    experience: "",
    interest: "",
    clients: "",
    info: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState<{
    type: "success" | "error" | null;
    message: string;
  }>({ type: null, message: "" });

  if (!data) return null;

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setSubmitStatus({ type: null, message: "" });

    try {
      const payload = {
        data: {
          name: formData.name,
          email: formData.email,
          phone: formData.phone,
          location: formData.location,
          instagram: formData.instagram || undefined,
          experience: formData.experience,
          interest: formData.interest,
          clients: formData.clients ? parseInt(formData.clients) : undefined,
          info: formData.info || undefined,
        },
      };

      const strapiURL = getStrapiURL();
      const response = await api.post<TContactForm>(
        `${strapiURL}/api/contact-forms`,
        payload
      );

      if (response.success) {
        setSubmitStatus({
          type: "success",
          message: "Thank you! Your submission has been received.",
        });
        setFormData({
          name: "",
          email: "",
          phone: "",
          location: "",
          instagram: "",
          experience: "",
          interest: "",
          clients: "",
          info: "",
        });
      } else {
        setSubmitStatus({
          type: "error",
          message:
            response.error?.message || "Something went wrong. Please try again.",
        });
      }
    } catch {
      setSubmitStatus({
        type: "error",
        message: "Failed to submit form. Please try again.",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto px-4 py-6">
        <div className="rounded-xl border bg-white my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col">
            <div className="mb-8 text-center">
                <h2 className="text-3xl font-bold mb-2">{data.heading}</h2>
                <p className="text-muted-foreground">{data.subHeading}</p>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                <FieldGroup className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Name */}
                <Field>
                    <FieldLabel htmlFor="name">{data.name}</FieldLabel>
                    <input
                    id="name"
                    name="name"
                    type="text"
                    required
                    value={formData.name}
                    onChange={handleChange}
                    placeholder="Enter your full name"
                    className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                    />
                </Field>

                {/* Email */}
                <Field>
                    <FieldLabel htmlFor="email">{data.email}</FieldLabel>
                    <input
                    id="email"
                    name="email"
                    type="email"
                    required
                    value={formData.email}
                    onChange={handleChange}
                    placeholder="your.email@example.com"
                    className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                    />
                </Field>

                {/* Phone */}
                <Field>
                    <FieldLabel htmlFor="phone">{data.phone}</FieldLabel>
                    <input
                    id="phone"
                    name="phone"
                    type="tel"
                    required
                    value={formData.phone}
                    onChange={handleChange}
                    placeholder="(123) 456-7890"
                    className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                    />
                </Field>

                {/* Location */}
                <Field>
                    <FieldLabel htmlFor="location">{data.location}</FieldLabel>
                    <input
                    id="location"
                    name="location"
                    type="text"
                    required
                    value={formData.location}
                    onChange={handleChange}
                    placeholder="Your City"
                    className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                    />
                </Field>

                {/* Instagram */}
                <Field>
                    <FieldLabel htmlFor="instagram">{data.instagram}</FieldLabel>
                    <input
                    id="instagram"
                    name="instagram"
                    type="text"
                    value={formData.instagram}
                    onChange={handleChange}
                    placeholder="@your_username"
                    className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                    />
                </Field>

                {/* Experience */}
                    <Field>
                        <FieldLabel htmlFor="experience">
                        {data.experience}
                        </FieldLabel>
                        <Select
                        value={formData.experience}
                        onValueChange={(value: string) =>
                            setFormData((prev) => ({ ...prev, experience: value }))
                        }
                        required
                        >
                        <SelectTrigger id="experience">
                            <SelectValue placeholder="Select experience level" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="Beginner - New to Lashes">Beginner - New to Lashes</SelectItem>
                            <SelectItem value="Advanced - Have Experience">Advanced - Have Experience</SelectItem>
                        </SelectContent>
                        </Select>
                    </Field>

                    {/* Clients */}
                    <Field>
                        <FieldLabel htmlFor="clients">
                        {data.clients}
                        </FieldLabel>
                        <input
                        id="clients"
                        name="clients"
                        type="number"
                        value={formData.clients}
                        onChange={handleChange}
                        placeholder="e.g., 10"
                        className="flex h-9 w-full rounded-md border border-input bg-transparent px-3 py-1 text-base shadow-sm transition-colors placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                        />
                    </Field>
                    
                    {/* Interest */}
                    <Field>
                        <FieldLabel htmlFor="interest">
                        {data.interest}
                        </FieldLabel>
                        <Select
                        value={formData.interest}
                        onValueChange={(value: string) =>
                            setFormData((prev) => ({ ...prev, interest: value }))
                        }
                        required
                        >
                        <SelectTrigger id="interest">
                            <SelectValue placeholder="Select training interest" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="Two-Week Mentorship Course">Two-Week Mentorship Course</SelectItem>
                            <SelectItem value="Beginner Private Training">Beginner Private Training</SelectItem>
                            <SelectItem value="Advanced Private Training">Advanced Private Training</SelectItem>
                            <SelectItem value="Not Sure Yet">Not Sure Yet</SelectItem>
                        </SelectContent>
                        </Select>
                    </Field>
                </FieldGroup>

                {/* Info - Full Width */}
                <Field>
                <FieldLabel htmlFor="info">{data.info}</FieldLabel>
                <Textarea
                    id="info"
                    name="info"
                    rows={4}
                    value={formData.info}
                    onChange={handleChange}
                    placeholder="Tell us more about your goals and what you hope to achieve..."
                    className="flex min-h-20 w-full rounded-md border border-input bg-transparent px-3 py-2 text-base shadow-sm placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring disabled:cursor-not-allowed disabled:opacity-50 md:text-sm"
                />
                </Field>

                {/* Submit Status */}
                {submitStatus.type && (
                <div
                    className={`p-4 rounded-md ${
                    submitStatus.type === "success"
                        ? "bg-green-50 text-green-800 dark:bg-green-900/20 dark:text-green-400"
                        : "bg-red-50 text-red-800 dark:bg-red-900/20 dark:text-red-400"
                    }`}
                >
                    {submitStatus.message}
                </div>
                )}

                {/* Submit Button */}
                <Button type="submit" disabled={isSubmitting} className="w-full">
                {isSubmitting ? "Submitting..." : "Submit"}
                </Button>
            </form>
      </div>
    </div>
  );
}

// ============================================
// COMBINED CONTACT PAGE LAYOUT
// ============================================

export interface IContactPageLayoutProps {
  scheduleData: IScheduleProps;
  contactInfoData: IContactInfoProps;
  contactFormData: IContactFormLabelsProps;
}

export function ContactPageLayout({data}:{data: IContactPageLayoutProps}) {
  return (
    <div className="bg-brand-cream mx-auto px-4 py-12">
        <div className="grid grid-cols-1 lg:grid-cols-5 gap-8">
            {/* Left Column - Schedule and Contact Info (2/5) */}
            <div className="space-y-8 lg:col-span-2">
                {data.contactInfoData && <ContactInfo data={data.contactInfoData} />}
                {data.scheduleData && <Schedule data={data.scheduleData} />}
            </div>

            {/* Right Column - Contact Form (3/5) */}
            <div className="lg:col-span-3">
                {data.contactFormData && <ContactFormLabels data={data.contactFormData} />}
            </div>
        </div>
    </div>
  );
}
